import React, { Component } from "react";
import axios from "axios";
import isValid from '../utils/ValidityChecker'
// import * as Sentry from '@sentry/browser'
export default class Demo extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }


    /**
    * update address using reverse geocoding of Algolia PLaces to obtain city, state, country, cityID
    */
    updateAddress = async (latlong) => {

        try {

            const data = (
                await axios.get(`https://dataservice.accuweather.com/locations/v1/cities/geoposition/search?apikey=jdJtjY5LHZvdj0IS8iiRqturSfo6sjq3&q=${latlong}`)
            ).data

            if (isValid(data)) {
                const city = data.AdministrativeArea.EnglishName
                const code = data.Key
                this.setState({ city: city })
                this.props.handleOnChange(city, code, 1)
            }

        } catch (error) {
            console.log(error)
        }
    }
    formatCoords = (latitude, longitude) => {
        return `${latitude},${longitude}`
    }
    getAddress = async () => {
        if ('geolocation' in navigator) {
            await navigator.geolocation.getCurrentPosition(

                async (position) => {
                    const latlong = this.formatCoords(
                        position.coords.latitude,
                        position.coords.longitude
                    )
                    this.updateAddress(latlong)
                },
                (error) => {
                    console.error(error)
                }
            )
        }
    }
    componentDidMount() {
        this.getAddress()
    }

    render() {
        return (
            <div>
                <h4>Using geolocation JavaScript API in React</h4>
                <span>your location :{this.state.city || "please confirm access to your location"}</span>
            </div>
        );
    }
}